.popup {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  visibility: hidden;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  transition: visibility 0.2s, opacity 0.3s ease-out;
  opacity: 0;
  background-color: rgba(0, 0, 0, .5);
}
